.programmCenterBlock {
	padding-top: 50px;
	padding-bottom: 50px;
	&__title {
		font-family: $blogger;
		font-weight: 700;
		font-size: $big-font-size;
		text-transform: uppercase;
		line-height: 33px;
		text-align: center;
		color: $orange;
		margin: 0;
		@include md(font-size, 24px); } }
.programmCenterContainer {
	@include row-flex();
	justify-content: flex-end;
	align-items: center; }
.programmButton {
	@include col();
	@include size(3);
	@include size-xl(6);
	@include size-md(12);
	margin-top: 30px; }
